import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';

function Rooms (props){

	let { id } = useParams();
	let findItem = props.rooms.find(function(findItem){
		return findItem.id == id
	});
	let history = useHistory();
	let [myAlert, myAlertEdit] = useState(true);

	useEffect(()=>{
		let timer = setTimeout(()=>{
			myAlertEdit(false);
		}, 2000);
		return ()=>{ clearTimeout(timer) };
	},[]);

	return (
		<div className="container rooms">

			{
				myAlert === true
				? (
					<div className="my-alert">예약 인원이 얼마 남지 않았습니다.</div>
				)
				: null
			}

			<div className="row">
				<div className="col-md-6">
				<img src={'../img/room_' + (findItem.id + 1) + '.jpg'}/>
				</div>
				<div className="col-md-6 mt-4">
				<h4 className="pt-5">{findItem.title}</h4>
				<p>{findItem.price}</p>
				<button className="btn btn-danger">예약하기</button> 
				<button className="btn btn-danger" onClick={()=>{history.goBack()}} >뒤로가기</button> 

				</div>
			</div>
		</div> 
	)
}

export default Rooms;