import React from 'react';

function Cards (props){
	return (
		<div className="col-md-4">
			<div className="item">
				<img src={'./img/room_'+ (props.i +1) +'.jpg'}/>
				<h4>{props.rooms.title}
					<span onClick={()=>{props.likeEdit(props.like + 1)}}>❤️</span>
					{props.like}
				</h4>
				<p>{props.rooms.price}</p>
			</div>
		</div>
	)
}

export default Cards;